.features .box {
    padding: 25px;
}
.features .box h2 {
    margin: 20px 0 20px 0;
    text-align: center;
    font-size: 20px;

}
.features .box p {
    word-spacing: 2px;
    transition: 0.5s;
    text-align: center;
}
.features .box i{
    font-size: 30px;
    margin-top: 20px;
    transition: 0.5s;
    opacity: 0;
}
.features .box:hover p{
    color: white;
}
.features .box:hover i{
    color: white;
    opacity: 1;
}

.btnn_shadow {
    padding: 18px 25px;
    border-radius: 6px;
    transition: 0.3s all ease;
    cursor: pointer;
    color: #ff014f;
    transition: 0.5s;
  }
  .btnn_shadow {
    background: linear-gradient(145deg, #ffffff, #ffffff);
    box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
  }
  .btnn_shadow:hover {
    background: #ff014f;
    color: white;
    transform: translateY(-10px);
  }

.imgalignfortools{
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 50%;
}

.ttgrid{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 35px;
    width: 100%;
  }

   /* Mobile Devices */
  @media only screen and (min-width: 320px) and (max-width: 480px) {
    
    .ttgrid {
      grid-template-columns: repeat(1, 1fr);
    }
    .imgalignfortools {
      width: 49%;
    }
    }

     /* iPads, Tablets */
    @media only screen and (min-width: 481px) and (max-width: 768px) {
    
      .ttgrid {
        grid-template-columns: repeat(3, 1fr);
      }
    
      }